import { Injectable, OnInit } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from '@angular/fire/firestore';
import { Proyectos } from '../models/proyectos';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class FirebaseService{

  private projectsCollection: AngularFirestoreCollection<Proyectos>;
  projects: Observable<Proyectos[]>;


  constructor( private firebase: AngularFirestore) { }

  getProyects(){
    this.projectsCollection = this.firebase.collection<Proyectos>('proyectos');
    console.log(this.projectsCollection.valueChanges());
    return this.projects = this.projectsCollection.valueChanges();
  }

}
